<template>
    <div class="register">
        <form>
            <input type="text" value="" placeholder=""
                v-model="phone"
            >
            <div class="send">
                <input type="text" placeholder=""
                    v-model="code"
                >
                <!--  -->
                <input type="button"
                    @click="seedCode"               
                    :value="codeText"
                    :disabled="send"
                    :class="{'btnActiveColor': sendcode, 'btnActive': codein,'send-btn':funshow}"
                    v-bind="teSting"
                    v-show="!test"
                >
                <!--   -->
                <input type="button" class="send-btn"
                    :value="codeText"
                    @click="testCode"
                    v-show="test"
                >
            </div>
            <input type="password" placeholder=""
                v-model="password"
            >
            <div class="register-btn">
                <input type="button" value=""
                    v-bind="registerIng" :disabled="isShow"
                    :class="{'isActiveColor': sendregis,'isActive': regisin}"
                    @click="postRegister"
                >
            </div>
        </form>
    </div>
</template>

<script>
import cookie from '../../../axios/cookie.js'
import { getcode, register } from '../../../api/api'

export default {
    data() {
        return {
            phone: '',
            code: '',
            password: '',
            isShow: false,      //  input
            sendregis: false,   // 
            regisin: true,      // 
            codeText: '',
            // 
            send: false,        //  input
            funshow: true,      // send
            sendcode: true,     // send
            codein: false,      // send
            test: true,         // 
            phones: ''          // 
        }
    },
    methods: {
        // 
        seedCode() {
            // console.log(111)
            var that = this;    // this 
            var countdown = 60;
            codetime();

            function codetime() {
                // console.log(222)
                if(countdown == 0) {
                    that.codeText = '';                    
                    countdown = 60;                    
                    that.send = false;
                    // 
                    that.sendcode = true;
                    that.codein = false;
                    return;
                } else {
                    countdown--;
                    that.codeText= countdown + "";
                    that.send = true;
                    //                     
                    that.sendcode = false;
                    that.codein = true;
                }
                setTimeout(function() {
                    codetime();
                },1000)
            }
            // 
            getcode({
                mobile: that.phone
            }).then((res) => {

            }).catch((error) => {
                //   ()
                that.phones = error.mobile[0];
                that.showToast()
            })
        },

        // 
        postRegister() {
            register({
                username: this.phone,
                code: this.code,
                password: this.password
            }).then((res) => {
                console.log(res)
                // cookie 7
                cookie.setCookie('name',res.data.username,7); // 
                cookie.setCookie('token',res.data.token,7); // token ()
                this.$store.dispath('users')
                // vuex
                this.$store.dispatch('users')
                // 
                this.$router.push('/')                
            }).catch((error) => {
                console.log(error)
                this.phone = error.mobile[0];
                this.code = error.code[0];
                this.showToast();
            })
        },

        testCode() {
            this.phones = '';   // 
            this.showToast();   // 
        },

        //  
        showToast() {
            this.$popup(this.phones)
        }        
    },
    computed: {
        //  input
        registerIng() {
            if(this.phone && this.code && this.password != "") {
                this.isShow = false;
                //  
                this.sendregis = true;
                this.regisin = false;
                // 
                this.test = true;
            } else {
                this.isShow = true;
                //  
                this.sendregis = false;
                this.regisin = true;
                // 
                this.test = false;
            }
        },
        //  send
        teSting() {
            if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.phone))) {
                this.send = true;
                //  
                this.sendcode = false;
                this.codein = true;
            } else {
                this.send = false;
                //  
                this.sendcode = true;
                this.codein = false;
            }
        }
    }
}
</script>

<style scoped>
input {
    width: 100%;
    display: block;
    padding: .2rem 0;
    border: .02rem solid #909090;
    border-radius: .08rem;
    text-indent: .16rem;
}

.register {
    padding: 0 .5rem;
}

.send {
    display: flex;
    margin: .4rem 0;
}

.send input:first-child {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-right: 0;
}

.send-btn {
    width: 50%;
    border-left: 0;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border: 0;
    background: #3091e7;
    color: #fff;
    text-align: center;
    color: .32rem; 
}

.register-btn {
    margin-top: .8rem;
}

.register-btn input {
    border-radius: 1rem;
    border: 0;
    font-size: .4rem;
    text-align: center;
    color: #fff;
}

.isActiveColor {
    background: #02bf03;
}

.isActive {
    background: #ccc;
}

.btnActiveColor {
    background: #3091e7;
}

.btnActive {
    background: #ccc;
}


</style>


