import vue from 'vue'

// 
import Popup from './popup.vue'

// vue 
const Popupctor = vue.extend(Popup)

//   
function showToast(text, time=2000) {
    // 
    const toastDome = new Popupctor({
        // 
        el: document.createElement('div'),
        data() {
            return {
                text: text,
                showwrap: true  // 
            }
        }
    })

    //   body
    document.body.appendChild(toastDome.$el)

    // 
    setTimeout(function() {
        toastDome.showwrap = false
    }, time)
}

// 
function popupToast() {
    // vue  this.$popup
    vue.prototype.$popup = showToast
} 

// 
export default popupToast


